<template>
	<view class="container">


		<view v-if="orders.length > 0">
			<view v-for="order in orders" :key="order.id" :title="`订单号: ${order.orderNumber}`">
				<view class="order-info">
					<u-row>
						<u-col span="6">
							<text>课程天数: {{ order.courseDays || '未指定' }}</text>
						</u-col>
						<u-col span="6">
							<text>费用: ¥{{ order.fee }}</text>
						</u-col>
					</u-row>
					<u-row>
						<u-col span="6">
							<text>报名时间: {{ order.registrationTime || '未指定' }}</text>
						</u-col>
						<u-col span="6">
							<text>审批状态:
								<u-tag :text="order.isApproved" :type="getApprovalTagType(order.isApproved)" />
							</text>
						</u-col>
					</u-row>
					<u-row>
						<u-col span="6">
							<text>支付状态:
								<u-tag :text="order.isPaid" :type="order.isPaid === '未付款' ? 'error' : 'success'" />
							</text>
						</u-col>
					</u-row>
				</view>

				<u-button type="primary" size="mini" v-if="order.isPaid === '未付款'"
					@click="payOrder(order)">去支付</u-button>
				<u-divider />
			</view>
		</view>
		<u-empty v-else text="暂无订单" mode="list" />
	</view>
</template>

<script>
	export default {
		data() {
			return {
				orders: []
			};
		},
		onShow() {
			this.init();
		},
		methods: {
			// 获取订单信息
			init() {
				uni.$u.http.get(`/classRegistrationOrder?parentId=${uni.getStorageSync('userId')}`)
					.then(res => {
						this.orders = res.data;
					})
					.catch(err => {
						console.error("获取订单失败", err);
					});
			},
			// 获取审批状态的标签类型
			getApprovalTagType(status) {
				switch (status) {
					case "通过":
						return "success";
					case "待审批":
						return "warning";
					case "未通过":
						return "error";
					default:
						return "info";
				}
			},
			// 支付订单
			payOrder(order) {
				uni.showToast({
					title: `支付订单 ${order.orderNumber}`,
					icon: "none"
				});
			}
		}
	};
</script>

<style>
	.container {
		padding: 20rpx;
	}

	.order-info {
		margin-top: 10rpx;
	}
</style>